import React,{useState} from 'react';
import Analytics from '../service/Firebase';

const Contact = () => {
    let name,value;
    const postUserData=(event)=>{
        name=event.target.name;
        value=event.target.value;
        setuserData({...userData,[name]:value})
    }
    const [userData, setuserData]=useState({
        name:'',
        email:'',
        mobile:'',
        message:''

    })
    const handleData= async (event)=>{
        event.preventDefault();
        const {name, email , mobile, message} = userData;
        // https://contactproject-98470-default-rtdb.firebaseio.com/
        const response= await fetch('https://contactproject-98470-default-rtdb.firebaseio.com/testdata.json',{
            method:'POST',
            headers:{
                'Content-Type':'application/json',
            },
            body:JSON.stringify({
                name,
                email,
                mobile,
                message
            })
        })
        if(response){
            setuserData({
            name:'',
            email:'',
            mobile:'',
            message:''
            })
            alert("data added successfully");
        }

    }
    return (
        <div>
            <div id="contact" className="contact">
                <div className="container-fluid padding_left2">
                    <div className="white_color">
                        <div className="row">

                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12">
                            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3602.569990870974!2d78.59637891478854!3d25.452635983777974!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x397777b103dba2c5%3A0xbe1ec4da11f83f85!2sBinplus%20Technologies%20Private%20Limited!5e0!3m2!1sen!2sin!4v1632831859334!5m2!1sen!2sin" width="100%" height="500" title="map" allowfullscreen="" loading="lazy"></iframe>

                            </div>
                            <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12">
                                <form className="contact_bg" onSubmit={handleData} method="POST">
                                    <div className="row">
                                        <div className="col-md-12">
                                            <div className="titlepage">
                                                <h2>Contact <strong className="yellow">us</strong></h2>
                                            </div>
                                            <div className="col-md-12">
                                            <input className="contactus" onChange={postUserData} placeholder="Your Name"  value={userData.name} type="text" name="name" />
                                            </div>
                                            <div className="col-md-12">
                                            <input className="contactus" onChange={postUserData} placeholder="Your Email"  value={userData.email} type="text" name="email" />
                                            </div>
                                            <div className="col-md-12">
                                            <input className="contactus" onChange={postUserData} placeholder="Your Phone"  value={userData.mobile} type="text" name="mobile" />
                                            </div>
                                            <div className="col-md-12">
                                            <textarea className="textarea" onChange={postUserData} placeholder="Message"  value={userData.message} type="text" name="message"></textarea>
                                            </div>
                                            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12">
                                                <button className="send">Send</button>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
        
    )
}

export default Contact  